.People-container {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    text-align: left;
}

.People-loading {
    width: 100%;
    padding: 100px 0;
}

.People-person {
    border: none;
    margin: none;
    padding: 20px;
    border-bottom: 1px solid lightgrey;
}

.People-person:last-of-type {
    border-bottom: none;
}

.People-person .name {
    margin: 0 0 10px 0;
}

.People-person .stats ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.People-person .stats ul li {
    margin: 5px 0;
}

@media(min-width: 800px) {
    .People-container {
        width: 800px;
        margin: 0 auto;
    }
    .People-person {
        border: 1px solid lightgrey;
        margin: 10px 0;
    }
}


